* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
body{
  height: 110vh;
  font-family: 'Fjalla One', sans-serif;
  
}
.App{
  position: relative;
  
}
.navigation{
  background-color: #393e46;
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav-links{
  display: flex;
  width: 40%;
  justify-content: space-around;
  align-items: center;
}
ul{
  list-style: none;
}
.nav-links a{
  text-decoration: none;
  font-size: 17px;
  font-weight: bold;
  letter-spacing: 1.2px;
  color: #cbf1f5;
}
.side{
  height: 90vh;
  width: 20vw;
  background-color: #cbf1f5;
  color: #393e46;
}
.box{
  height: 30vh;
  text-align: center;
  vertical-align: middle;
  line-height: 30vh;      

}

 
.scrolled{
  position: fixed;
  top: 0;
  width: 100%;
}
.video{
    position:absolute;
     bottom:0;
     right:0;
     width: 400 ;
     height: 300
}
.main{
  width: 78.6vw;
  height: 90vh;
  position: relative;
  right: 0;
  background-color: #eeeeee;
  text-align: center;
  vertical-align: middle;
  line-height: 90vh; 
}

 @media screen and (max-width : 768px){
  .side{
    display: none;
  }
  .video{ 
    left:12vh;
    z-index: -1;
    top:30vh;
  }
} 

